import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    LoginState: {
      Lstate: false,
      username:""
    }
  },
  mutations: {
    getLoginState(state, current) {
      state.LoginState.Lstate = true;
      state.LoginState.username = current.username;
    },
    lastLoginState(state) {
       state.LoginState.Lstate = false;
       state.LoginState.username = "";
    }
  },
  getters: {
    getUser(state){
      return state.LoginState.username
    },
    getLoginS(state) {
      return state.LoginState.Lstate
    }
  },
  actions: {
    AcgetLoginState(context) {
      let current = Bmob.User.current()
      context.commit('getLoginState', current)
    },
  }
})
